<template>
  <div class="detail">
    <div class="main">
      <loadingComp :loading="loading" v-if="loading"></loadingComp>

      <div class="animate__animated animate__fadeIn main-box" v-if="!loading">
        <div class="detail-info">
          <div class="thumbnail"><img :src="thumbnail" alt=""></div>

          <div class="info-r">
            <div class="title">{{ title }}</div>

            <div class="list info-price">
              <div class="price"><label>: </label> <span class="text">{{ price | twoDecimal }}</span></div>
              <div class="sold"><label>: </label>{{ sold }} </div>
            </div>

            <div class="list stock"><label>: </label><span class="num">{{ stock }} </span></div>

            <div class="list service">
              <label>: </label>

              <div class="state">
                <span class="item" v-for="(s, index) in service" :key="index">{{ s.name }}</span>
              </div>
            </div>

            <div class="list spec-box">
              <label>: </label>

              <div class="spec">
                <span class="item" v-for="(item, index) in spec" :key="index" :class="{ active: item.active }" @click="calcPriceBySpec(item)">
                  {{ item.value }}{{ item.currency }}
                </span>
              </div>
            </div>

            <div class="list">
              <label>: </label>
              <el-input-number class="number" v-model="count" controls-position="right" @change="calcPriceByNum" :min="1" size="mini"></el-input-number>
            </div>

            <div class="list">
              <div class="buy" @click="BuyImediate(specActiveItem ?. notEnough)">{{ specActiveItem ?. notEnough ? '' : '' }}</div>
            </div>
          </div>
        </div>

        <div class="intro">
          <div class="head">
            <i class="iconfont icon-jieshao"></i>
            <span class="title"></span>
          </div>

          <div class="intro-content">
            {{ content }}
          </div>
        </div>

        <!--  -->
        <transition name="fade-back">
          <div class="back-shade" @click="closeOrderConfirm" v-if="showBuyImediateModal"></div>
        </transition>

        <transition name="fade">
          <div class="orderConfirm" v-if="showBuyImediateModal">
            <div class="order-content">
              <div class="head">
                <span></span>
                <span class="close" @click="closeOrderConfirm">&times;</span>
              </div>

              <div class="order-main">
                <div class="order-info">
                  <label>:</label>
                  <span class="item">{{ specActiveItem.value }}</span>
                </div>

                <div class="order-info">
                  <label>:</label>
                  <span class="item">{{ count }} </span>
                </div>

                <div class="order-info">
                  <label>:</label>
                  <span class="item price">{{ price }}</span>
                </div>

                <div class="order-info phone-box">
                  <label>:</label>
                  <span class="item phone">
                    <el-input type="text" v-model="phoneNumber" placeholder=""></el-input>
                  </span>
                </div>

                <div class="order-info email-box" v-if="showEmailBox">
                  <label>:</label>
                  <span class="item phone">
                    <el-input type="text" v-model="email" placeholder=""></el-input>
                  </span>
                </div>

                <div class="order-info send-email-box">
                  <label></label>
                  <span class="item phone" @click="showEmail">
                    <el-checkbox v-model="emailChecked">()</el-checkbox>
                  </span>
                </div>

                <div class="order-info remarks-box">
                  <label>:</label>
                  <span class="item remarks">
                    <el-input type="textarea" placeholder="" v-model="remarks" :rows="3"></el-input>
                  </span>
                </div>

                <div class="order-info">
                  <label></label>
                  <span class="item remarks">
                    <el-button type="primary" size="small" @click="doAlipay"></el-button>
                    <el-button type="info" size="small" disabled></el-button>
                    <el-button size="small" @click="closeOrderConfirm"></el-button>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: "DetailView",
  data () {
    return {
      loading: false,              // 
      timer: null,                 // setTimeout 

      thumbnail: '',               // 
      title: '',                   // 
      sold: 0,                     // 
      service: [],                 // 
      spec: [],                    // 
      content: '',                 // 
      stock: 0,                    // 
      price: 0,                    // 
      count: 1,                    // 

      showBuyImediateModal: false, //   
      showEmailBox: false,         // 
      phoneNumber: null,           // 
      email: '',                   // 
      emailChecked: false,         // 
      remarks: '',                 // 
    }
  },

  computed: {
    ...mapState(['detailPageData']),

    //  id
    id: {
        get () {
          return this.$route.params.id
        }
    },

    // 
    specActiveItem: {
      get () {
        return this.spec ?. filter(i => i.active)[0]
      }
    },

  },

  methods: {
    ...mapActions(['getDetailPageData']),

    // 
    loadStatus () {
      this.loading = true

      this.timer = setTimeout(() => {
        this.loading = false
      }, 1000)
    },

    // 
    getDetailData () {
      // 
      this.getDetailPageData()
      let res = this.detailPageData

      this.thumbnail = res.thumbnail
      this.title = res.title
      this.sold = res.sold
      this.service = res.service
      this.spec = res.spec
      this.content = res.content

      this.stock = this.specActiveItem.stock
      this.price = this.specActiveItem.price
    },

    // 
    showEmail (e) {
      this.showEmailBox = e.target.checked ? true : false
    },

    // 
    calcPriceBySpec (item) {
      this.price = item.price * this.count
      this.stock = item.stock
      this.spec.map(i => {
        if (i.id == item.id) {
          i.active = true
        }
        else {
          i.active = false
        }
      })
    },

    // 
    calcPriceByNum (num) {
      this.price = num * this.specActiveItem.price
    },

    // 
    BuyImediate (notEnough) {
      // 
      if (notEnough) return
      this.showBuyImediateModal = true
    },

    // 
    doAlipay () {
      // 
      let phoneReg = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/g
      let isCorrectPhoneNumber = phoneReg.test(this.phoneNumber)

      //  saf@sdf.com
      let emailReg = /^[a-zA-Z0-9]+([-_.][A-Za-z\d]+)*@([a-zA-Z0-9]+[-.])+[A-Za-z\d]{2,5}$/gi
      let isCorrectEmail = emailReg.test(this.email)

      // 
      if (!isCorrectPhoneNumber) {
        this.$message({
          type: 'warning',
          message: ''
        })
        return
      }

      // 
      if (this.emailChecked && !isCorrectEmail) {
        this.$message({
          type: 'warning',
          message: ''
        })
        return
      }

      // 
    },

    // 
    closeOrderConfirm () {
      this.showBuyImediateModal = false
    }

  },

  mounted() {
    // 
    this.loadStatus()
    // 
    this.getDetailData()
  },

  destroyed() {
    //  setTimeout 
    clearTimeout(this.timer)
  },

}
</script>

<style scoped lang="stylus">
.back-shade
  position fixed
  top 0
  left 0
  bottom 0
  right 0
  z-index 9
  background-color rgba(55,55,55,.6)

  &.show
    opacity 1
    transition .2s linear

  &.hide
    opaity 0
    transition .2s linear

.main-box
  padding 1rem
  background-color white

  .detail-info
    display flex
    margin-top .5rem

    .thumbnail
      width 40%
      border 1px solid #eee
      margin .5rem 2rem .5rem .5rem

      img
        width 100%
        height 100%
        object-fit cover

    .info-r
      flex 1
      color #999
      padding-right 1rem

      .title
        font-size 2rem
        margin 1rem 0
        color black
        padding-left 1rem

      .info-price
        background-color #f4f4f4
        border-radius 3px
        display flex
        justify-content space-between
        align-items center
        height 3rem
        padding 0 1.5rem

        .price
          color red
          display flex
          align-items center

          label
            color #999
            margin-right 1rem

          .text
            font-size 2.2rem
            color red

      .list
        padding 1rem
        display flex

        label
          min-width 4rem

        .num
          color #515a6e

        &.service .state
          .item
            background-color #519BE0
            border-radius 3px
            color white
            font-size 1.2rem
            padding 0.3rem .8rem 0.4rem
            margin-right .5rem

            &:hover
              opacity .85

            &:nth-child(2)
              background-color #6BE051

            &:nth-child(3)
              background-color #51E0B5

        &.spec-box
          padding-bottom 0

        .spec
          display flex
          flex-wrap wrap

          .item
            border 1px solid #cccccc
            border-radius 2px
            padding .5rem 1rem
            margin-right 1rem
            margin-bottom 1rem
            font-size 1.2rem
            cursor pointer

            &.active
              color #FF4500
              border-color #FF4500

        .number
          width 8rem
          height 3rem

          /deep/ .el-input__inner
            text-align left
            height 3rem
            padding 0
            padding-left 1rem

        .buy
          width 17.8rem
          height 3.8rem
          display flex
          justify-content center
          align-items center
          background-color #FFEDED
          border 1px solid #ff0036
          color #ff0036
          font-size 1.6rem
          margin-left 4rem
          cursor pointer

  .intro
    margin 0 .5rem
    margin-top 6rem
    border 1px solid #DFDFDF

    .head
      display flex
      align-items center
      padding 1rem
      border-bottom 1px solid #dfdfdf

      .title
        color black
        font-weight bold
        margin-left .5rem

    .intro-content
      padding 1rem

  .orderConfirm
    width 60rem
    min-height 46rem
    position fixed
    top calc(50% - 23rem)
    left calc(50% - 30rem)
    z-index 2000
    display flex
    justify-content center
    align-items center
    overflow hidden

    .order-content
      background white
      border-radius 6px
      width 100%
      height 100%

      .head
        height 5rem
        display flex
        justify-content space-between
        color #17233d
        font-size 1.6rem
        align-items center
        padding 0 2rem
        border-bottom 1px solid #E8EAEC

        .close
          font-size 2.6rem
          color #999
          font-weight lighter
          cursor pointer

          &:hover
            color #444

      .order-main
        padding 2rem

        .order-info
          display flex
          margin-bottom 2.5rem
          align-items center

          label
            width 7.6rem
            text-align right
            margin-right 2rem

          .item
            flex 1

            &.price
              color red
              font-size 2rem
              font-weight bold

            &.phone /deep/ .el-input__inner, &.remarks /deep/ .el-textarea__inner
              width 70%
              font-family "Microsoft YaHei"
              padding-left 1rem

            &.is_disabled
              color #c5c8ce

            & /deep/ .el-button
              font-size 1.4rem
              border-radius 4px

          &.phone-box, &.email-box
            margin-bottom .5rem

          &.send-email-box
            margin-bottom 2rem

          &.remarks-box
            margin-bottom 1.5rem

.fade-back-enter-active, .fade-back-leave-active
  transition .3s ease

.fade-back-enter, .fade-back-leave-to
  opacity 0

.fade-enter-active, .fade-leave-active
  transition .3s ease

.fade-enter, .fade-leave-to
  transform scale(0.9)
  opacity 0

</style>