<template>
  <div class="m-pay">
    <div class="pay-head">
      <div class="title"><i class="van-icon van-icon-balance-pay"></i> </div>
      <div class="money"><em></em> {{ finalPrice | twoDecimal }}</div>
    </div>

    <div class="pay-info">
      <van-form validate-first>
        <!--  pattern  -->
        <van-field
            v-model="phoneNumber"
            name="pattern"
            label=""
            label-width="6rem"
            placeholder=""
            :rules="[{ pattern, message: '' }]"
        />

        <van-field name="checkbox" label="" label-width="6rem">
          <template #input>
            <van-checkbox v-model="additionalService" shape="square">()</van-checkbox>
          </template>
        </van-field>

        <!--  validator  -->
        <van-field
            v-model="email"
            name="validator"
            label=""
            label-width="6rem"
            placeholder=""
            :rules="[{ validator, message: '' }]"
            v-if="additionalService"
        />

        <van-field
            v-model="remarks"
            rows="2"
            autosize
            label=""
            label-width="6rem"
            type="textarea"
            maxlength="100"
            placeholder=""
            show-word-limit
        />
      </van-form>
    </div>

    <div class="alert-info">
      ->
    </div>

    <div class="pay-footer">
      <van-button round type="info" @click="goToAlipay"><i class="iconfont icon-zhifubao"></i> </van-button>
      <van-button round disabled type="primary"><i class="iconfont icon-weixin1"></i> </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "PayView",
  data () {
    return {
      info: {}, // 
      phoneNumber: '',
      additionalService: false,
      email: '',
      remarks: '',
      pattern: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
    }
  },

  computed: {
    finalPrice () {
      return (this.info.selectedSkuComb?.price / 100) * this.info?.selectedNum
    },
  },

  methods: {
    //  true false 
    validator(val) {
      return /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(val)
    },

    // 
    getGoodInfo () {
      this.info = JSON.parse(this.$route.query.info)
    },

    // 
    goToAlipay () {
      // 
      if (!this.pattern.test(this.phoneNumber)) {
        this.$toast('')
        return
      }

      // 
      if (this.additionalService && !this.validator(this.email)) {
        this.$toast('')
        return
      }

      // 
      console.log(this.info)
    },

  },

  mounted() {
    // 
    this.getGoodInfo()
  }
}
</script>

<style scoped lang="stylus">
.m-pay
  min-height 100vh
  background-color #f5f6f7
  overflow hidden

  .pay-head
    background-color white
    text-align center
    margin 1rem
    border-radius 1rem 1rem 0 0
    position relative

    &:before
      content ''
      position absolute
      left 0
      right 0
      bottom 0
      height 2px
      background repeating-linear-gradient(-45deg,#ff6c6c,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%)
      background-size 8rem

    .title
      height 4rem
      line-height 4rem
      font-size 1.6rem
      border-bottom 1px solid #e8eaec

      i
        font-size 1.8rem
        margin-right .1rem
        vertical-align middle

    .money
      line-height 10rem
      font-size 3.5rem
      color red

      em
        font-size 2rem
        font-style normal
        margin-right -1rem

  .pay-info
    margin 0 1rem
    border-radius 1rem
    background-color white
    overflow hidden

  .alert-info
    margin 1rem
    background-color #f0faff
    border 1px solid #fff
    padding .8rem
    border-radius 1rem
    line-height 1.6rem

  .pay-footer
    position fixed
    bottom 0
    left 0
    padding .8rem
    display flex
    justify-content space-between
    width calc(100% - 1.6rem)

    .van-button
      width 48%
      height 4.4rem

      .van-button__text
        display flex
        align-items center

        i
          margin-right .5rem

</style>