<template>
  <div class="m-search">
    <Search :search-value.sync="searchValue" :autofocus="true" :placeholder="''" :do-search="searchGoods" :right-text="''" ref="search" />

    <div class="empty" v-if="showGoodsList && searchList.length == 0">
      <el-empty :image-size="100" description=""></el-empty>
    </div>

    <div class="recommend-list" v-if="!showGoodsList">
      <div class="title"></div>

      <div class="list">
        <div class="item" v-for="(item, index) in recommendList" :key="index" @click="doSearchByRec(item.title)">{{ item.title }}</div>
      </div>
    </div>

    <GoodsList :list="searchList" :not-more="false" v-show="showGoodsList" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import GoodsList from "@/views/mobile/components/GoodsList"
import Search from "@/views/mobile/components/Search"

export default {
  name: "MSearch",
  components: {
    GoodsList,
    Search,
  },
  computed: {
    ...mapState(['goodsList']),
  },
  data () {
    return {
      searchValue: '', // 
      showGoodsList: false, // 
      recommendList: [], // 
      searchList: [], // 
    }
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 
    getRecommendList () {
      let res = [
        { id: 1, title: '-Apple iTunes ' },
        { id: 2, title: '' },
        { id: 3, title: '-Apple' },
        { id: 4, title: '-Apple' },
        { id: 5, title: '-Google Play ' },
        { id: 6, title: '-Apple iTunes ' },
        { id: 7, title: '-Steam ' },
        { id: 8, title: '-Amazon ' },
        { id: 9, title: '-Apple ' },
      ]

      this.recommendList = res
    },

    // 
    searchGoods () {
      if (!this.searchValue) {
        this.getGoodsList()
        this.showGoodsList = true
        this.searchList = this.goodsList
        return
      }

      this.getGoodsList()
      this.showGoodsList = true
      this.searchList = this.goodsList.filter(i => i.title.includes(this.searchValue))

      // 
      this.$refs.search.$el.querySelector('input').focus()
    },

    // 
    clearValue () {
      this.searchValue = ''
    },

    //   
    doSearchByRec (title) {
      this.showGoodsList = true
      this.searchValue = title
      this.searchGoods()
    },

  },

  mounted() {
    this.getRecommendList()
  }
}
</script>

<style scoped lang="stylus">
.m-search
  .recommend-list
    padding 1.5rem
    padding-top .5rem
    background-color white

    .title
      margin-bottom 1rem

    .list
      display flex
      flex-wrap wrap

      .item
        color #686868
        font-size 1.2rem
        background-color #f0f2f5
        margin 0 1rem 1rem 0
        padding 0 1.3rem 0 1.3rem
        border-radius 3px
        height 2.4rem
        line-height 2.4rem

</style>